@import url(https://fonts.googleapis.com/css2?family=Square+Peg&display=swap);
body {
  margin: 0;
  padding: 0;
  background: linear-gradient(
      to bottom left,
      #6cf3f5,
      #91cefb,
      #b7b3ce,
      #efaed0,
      #f1b8fc
    );
    background-repeat: no-repeat;
    background-size: cover;
    overflow: hidden;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    font-family: 'Square Peg', cursive;
}


.cat-container {
    display: flex;
    justify-content: center;
    vertical-align: middle;
    -webkit-perspective: 500px;
            perspective: 500px;
  }
  .spacecat {
    width: 300px;
    height: 300px;
    -webkit-animation: rotation 6s infinite linear;
            animation: rotation 6s infinite linear;
  }

  @-webkit-keyframes rotation {
    from {
      -webkit-transform: rotate(1deg);
              transform: rotate(1deg);
    }
    to {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
    }
  }

  @keyframes rotation {
    from {
      -webkit-transform: rotate(1deg);
              transform: rotate(1deg);
    }
    to {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
    }
  }

  p {
    font-size: 22px;
    font-weight: 600;
    display: flex;
    text-align: center;
    opacity: 0;
    width: 100%;
  }

  .text{
    top: 0;
    left: 0;
    position: absolute;
    display: flex;
    align-items: center;
  }

  .t1{
    -webkit-animation: t-animation 6s;
            animation: t-animation 6s;
    align-items: center;
  }

  .t2{
    -webkit-animation: t-animation 6s;
            animation: t-animation 6s;
    -webkit-animation-delay: 6s;
            animation-delay: 6s;
    align-items: center;
  }

  .t3{
    -webkit-animation: t-animation 6s;
            animation: t-animation 6s;
    -webkit-animation-delay: 10s;
            animation-delay: 10s;
    align-items: center;
  }

  .end{
    -webkit-animation: end-animation 2s;
            animation: end-animation 2s;
    -webkit-animation-delay: 12s;
            animation-delay: 12s;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    align-items: center;
  }

  .flicker{
    -webkit-animation: flicker 10s infinite;
            animation: flicker 10s infinite;
    align-items: center;
  }

  @-webkit-keyframes t-animation {
    0% {opacity: 0;}
    30% {opacity: 1;}
    70% {opacity: 1;}
    100% {opacity: 0;}
  }

  @keyframes t-animation {
    0% {opacity: 0;}
    30% {opacity: 1;}
    70% {opacity: 1;}
    100% {opacity: 0;}
  }

  @-webkit-keyframes end-animation {
    0% {opacity: 0;}
    100% {opacity: 1;}
  }

  @keyframes end-animation {
    0% {opacity: 0;}
    100% {opacity: 1;}
  }

  @-webkit-keyframes flicker {
    40.1% {left:0px;top:0px;}
    40.2% {left:10px;top:8px;}
    40.3% {left:0px;top:0px;}

    90.1% {left:0px;top:0px;}
    90.2% {left:-5px;top:3px;}
    90.3% {left:0px;top:0px;}
  }

  @keyframes flicker {
    40.1% {left:0px;top:0px;}
    40.2% {left:10px;top:8px;}
    40.3% {left:0px;top:0px;}

    90.1% {left:0px;top:0px;}
    90.2% {left:-5px;top:3px;}
    90.3% {left:0px;top:0px;}
  }
#tsparticles {
    position: fixed;
    height: 100%;
    width: 100%;
    margin: 0;
    padding: 0;
    left: 0;
    top: 0;
    z-index: -1;
}
@media screen and (max-width: 768px) and (min-width: 280px) {
    body {
      overflow-x: hidden;
      overflow-y: hidden;
    }}
